<template>
  <div class="login-page">
    <img class="logo_left" src="@assets/images/logo_left.png" alt="" />
    <img class="logo_body" src="@assets/images/logo_body.png" alt="" />
    <img class="logo_tail" src="@assets/images/logo_tail.png" alt="" />
    <div class="login-content">
      <div class="logo_login">
        <!-- <div class="logo_login_chs">运营管理平台</div>
        <div class="logo_login_eng">Operations Management Platform</div> -->
        <img class="" src="@assets/images/logo_title.png" alt="" />
      </div>
      <div class="from-wrapper">
        <div class="user_pass">
          <img
            class="login_iptimg"
            src="@assets/images/login_user.png"
            alt=""
          />
          <el-input
            v-model.trim="userInfo.username"
            class="user-name"
            placeholder="登录账号"
          >
          </el-input>
        </div>
        <div class="user_pass">
          <img
            class="login_iptimg"
            src="@assets/images/login_password.png"
            alt=""
          />
          <el-input
            v-model="userInfo.password"
            class="user-pwd"
            placeholder="密码"
            :type="!eyeFlag ? 'password' : 'text'"
            @keyup.enter.native="login"
          >
          </el-input>
        </div>

        <span id="tip" class="tishi"></span>
        <div class="rememberInfo">
          <el-checkbox v-model="remeName">记住账号</el-checkbox>
          <el-checkbox v-model="remePwd">记住密码</el-checkbox>
        </div>
        <el-button class="login-button" @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { setStorage, getStorage, removeStorage } from '@utils/storage';
import Base64 from '@utils/Base64';
import { queryUserStation } from '@src/api/user';
export default {
  name: 'Login',
  data() {
    return {
      userInfo: {
        username: '',
        password: ''
      },
      eyeFlag: false,
      remeName: true,
      remePwd: false
    };
  },
  mounted() {
    this.getRemeInfo();
  },
  methods: {
    openEyes() {
      this.eyeFlag = false;
    },
    closeEyes() {
      this.eyeFlag = true;
    },
    getRemeInfo() {
      this.userInfo.username = getStorage('username');
      if (getStorage('password')) {
        this.userInfo.password = Base64.decode(getStorage('password'));
      }
    },
    remeInfo() {
      if (this.remeName) {
        setStorage('username', this.userInfo.username);
      } else {
        removeStorage('username');
      }
      if (this.remePwd) {
        setStorage('password', Base64.encode(this.userInfo.password));
      } else {
        removeStorage('password');
      }
    },
    userStation() {
      this.$store.commit('SET_USER_STATION', {});
      queryUserStation().then(({ data, error }) => {
        if (!error) {
          this.$store.commit('SET_USER_STATION', data.rows);
        } else {
          this.$message.error(error.message);
        }
      });
    },
    // 首次登录，建议修改密码
    firstLogin() {
      // 此提醒应放到首页提醒，不确定是否添加此功能，这里是测试内容
      this.$confirm(
        '您是首次登录本系统，建议您修改默认密码提高账户安全性，是否立刻修改?',
        '欢迎新用户',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          // 确认修改，进入首页，打开修改密码弹框
          this.$router.push('/home');
        })
        .catch(() => {
          // 取消修改，直接进入首页
          this.$router.push('/home');
        });
    },
    login() {
      if (!(this.userInfo.username && this.userInfo.password)) {
        this.$alert('账户名或密码不能为空', '提示', {
          confirmButtonText: '确定'
        });
      } else {
        this.$store.dispatch('login', this.userInfo).then((state) => {
          switch (Number.parseInt(state)) {
            case 20101:
              this.remeInfo();
              this.userStation();
              this.$router.push('/home');
              break;
            case 20104:
              this.remeInfo();
              this.$router.push('/home');
              // this.firstLogin();
              break;
            case 20102:
              this.showMessagess(1);
              break;
            case 20103:
              this.showMessagess(6);
              break;
            // case 20104:
            //   this.showMessagess(1);
            //   break;
            case 20105:
              this.showMessagess(1);
              break;
            case 20106:
              this.showMessagess(1);
              break;
            case 20107:
              this.showMessagess();
              break;
            case 20108:
              this.showMessagess(4);
              break;
            case 20109:
              this.showMessagess(5);
              break;
            case 500:
              this.showMessagess(3);
              break;
          }
        });
      }
    },
    showMessagess(val) {
      const text = document.getElementById('tip');
      switch (val) {
        case 6:
          text.innerHTML = '用户名和密码不匹配，请重试';
          break;
        case 1:
          text.innerHTML = '当前用户已被锁定，请联系相关负责人';
          break;
        case 2:
          text.innerHTML = '用户名或者密码错误';
          break;
        case 3:
          text.innerHTML = '系统错误，请稍后再试';
          break;
        case 4:
          text.innerHTML = '用户已失效';
          break;
        case 5:
          text.innerHTML = '用户当前状态异常，请联系相关负责人';
          break;
        default:
          text.innerHTML = '账户未被激活，请联系相关负责人';
          break;
      }
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss">
.login-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url('../../assets/images/login_background.jpg') no-repeat;
  background-size: cover;
  .logo_left {
    position: absolute;
    top: 80px;
    left: 80px;
    width: 275px;
  }
  .logo_body {
    position: absolute;
    top: 290px;
    left: 0px;
    width: 72vw;
  }
  .logo_tail {
    position: absolute;
    bottom: 130px;
    left: 380px;
    width: 30vw;
  }
  .login-wrapper {
    position: relative;
    width: 1600px;
    min-width: 960px;
    height: 100%;
    box-shadow: 0 0 35px -20px;
  }
  .login-bg {
    position: relative;
    width: 936px;
    height: 100%;
    font-size: 0;
    .bg {
      width: 100%;
      height: 100%;
    }
  }
  .login-content {
    position: absolute;
    top: 0;
    right: 0;
    width: 600px;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    .logo_login {
      margin-top: 16.6vh;
      margin-bottom: 18.7vh;
      .logo_login_chs {
        font: bold 48px 'Microsoft YaHei';
        color: #333333;
      }
      .logo_login_eng {
        font: 18px 'Microsoft YaHei';
        color: #666666;
      }
    }
    .from-wrapper {
      .rememberInfo {
        width: 352px;
        margin: 0 auto;
        .el-checkbox:nth-child(1) {
          float: left;
        }
        .el-checkbox:last-child {
          float: right;
        }
        &::after {
          display: block;
          content: '';
          clear: both;
        }
      }
      .login-button {
        width: 360px;
        height: 50px;
        margin-top: 3vh;
        font-size: 18px;
        color: #fff;
        background: #ffbe04;
        border: none;
        border-radius: 25px;
        &:hover {
          border-color: #f5b202;
          background-color: #f5b202;
        }
        &:active {
          border-color: #efad02;
          background-color: #efad02;
        }
      }
      .el-input__inner {
        width: 360px;
        height: 50px;
        padding-left: 54px;
        font-size: 18px;
        border: 1px solid #b6b6b6;
        border-radius: 25px;
        &:focus {
          border-color: $color-main;
        }
      }
      .svg-icon {
        width: 24px;
        height: 24px;
        margin-left: 20px;
        color: red;
        vertical-align: -2.4em;
      }
      .el-input__suffix {
        right: 20px;
      }
      .tishi {
        color: #f56c6c;
      }
    }
    .user_pass {
      width: 360px;
      height: 50px;
      margin: 0 auto;
      position: relative;
      margin-bottom: 2vh;
      .login_iptimg {
        position: absolute;
        top: 14px;
        left: 24px;
        z-index: 1;
      }
    }
  }
}
</style>
